@mixin left-top{
  position:absolute;
  top:0;
  left:0;
  @content;
}

.container{
  width:100px;
  height:100px;
  background-color:orange;
  @include left-top;
}

.box{
  width:100px; 
  height:100px;
  @include left-top{
    left:300px;
    padding:50px;
  }
  background-color:green;
}